  const formateTime = (dateString) => {
    if (!dateString) return null;

    // Parse the date as UTC
    const postDate = new Date(dateString);
    if (isNaN(postDate.getTime())) {
      console.error("Invalid date:", dateString);
      return "Invalid date";
    }

    // Get the current time in UTC
    const now = new Date();

    // Convert postDate to Europe/Oslo time
    const osloPostTime = new Intl.DateTimeFormat("en-GB", {
      timeZone: "Europe/Oslo",
      hour: "2-digit",
      minute: "2-digit",
      hour12: false,
    }).format(postDate);
    console.log(osloPostTime, postDate);
    // Calculate time difference
    const timeDiff = now.getTime() - postDate.getTime();
    const seconds = Math.floor(timeDiff / 1000);
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);
    const weeks = Math.floor(days / 7);
    const months = Math.floor(days / 30);
    const years = Math.floor(days / 365);

    // Generate formatted output
    if (minutes < 60) return `${osloPostTime}`;
    if (hours < 24) return `${osloPostTime}`;
    if (days === 1) return `Yesterday at ${osloPostTime}`;
    if (days < 7) return `${days}d  ${osloPostTime}`;
    if (weeks < 4) return `${weeks}w  ${osloPostTime}`;
    if (months < 12) return `${months}m  ${osloPostTime}`;
    return `${years}y  ${osloPostTime}`;
  };

